html, body {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Alegreya Sans', sans-serif;
}
*, *:before, *:after {
    box-sizing: inherit;
}

/* top nav bar */

.nav {
    display: flex;
    align-items: center;
    justify-content: space-around;
    
    width: 100vw;

    background: #ffffff;

    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;

}

.nav__logobox {
    flex: 1;
    margin-left: 1rem;
}

.nav__logo {
    height: 2.4rem;
    align-self: center;
}

.nav__link {
    background: #444444;
    color: #ffffff;

    text-transform: uppercase;
    text-decoration: none;
    text-align: center;
    font-size: 1.2rem;
    font-weight: 300;
    
    padding: 0.8rem;
    flex: 1;
}

.nav__link:hover {
    background: #54be90;
}

/* welcome splash with leaves */

.welcome {
    height: 30rem;
    background-image: url(/img/polar2.jpg);
    background-size: cover;
    text-align: center;
    margin-top: 8rem;
    padding-top: 5rem;;
} 

.welcome__blurb {
    color: #ffffff;
    font-weight: 400;
    font-size: 3.5rem;
}

.welcome__blurb > h1 {
    display: inline;
    color: #54be90;
    font-weight: 500;
    font-size: 4rem;

    text-decoration: underline white 0.4rem ;
    text-underline-offset: 0.3rem;
}

.welcome__job > h2{
    color: #ffffff;
    font-weight: 300;
    font-size: 3rem;
    margin: 0;
}

/* mission */

.mission {
    width: 100vw;
}

.mission__text {
    margin: auto;
    width: 80%;

    background: white;
    color: black;
    text-align: center;
    font-size: 1.5rem;
    font-weight: 200;
    padding: 2.5rem 0 3.5rem 0;

    line-height: 1.8rem;;
}
.mission--p {
    font-weight: 500;
}

/* offer cutouts */

.offer {
    width: 100vw;
    min-width: 800px;
    flex-wrap: nowrap;
    justify-content: space-around;
    flex-basis: 20%;
    background-color: #232323;

    display: flex;
    justify-content: space-around;
}

.offer__stage {
flex: 1;
padding-bottom: 2rem;
}

.offer__shape {
    
    margin: auto;
    height: 4rem;
    width: 8rem;
    background-color: white;
    border-radius: 0 0 4rem 4rem;
    margin-bottom: 2rem;
    flex: 1;
}

.offer__skill {
    text-align: center;
    color: #54be90;
    font-size: 1.75rem;
    font-weight: 700;
    margin-bottom: 1.5rem;
    flex: 1;
}

.offer__details {
    text-align: center;
    color: white;
    font-size: 1rem;
    font-weight: 200;
    margin: 1.25rem;
    flex: 1;
}

/* headers */

.header {
    width: 100vw;
    text-align: center;
    padding: 3.5rem 0 2rem 0;
}

.header--h2 {
    font-weight: 500;
    font-size: 4rem;
    margin: 0;
}

.green {
    color: #54be90;
}

.white {
    color: white;
}

.black {
    color: black;
}

/* skills table */

.skills {
    width: 100vw;
    background-color: white;
    padding: 0 0 4rem 0;
}

.skills__graphic {
    max-width: 60%;
    margin: auto;
    display: flex;
    flex-direction: column;
}

.skills__container {
    
    margin: auto;
    display: flex;
    margin: 0.3rem;
    height: 3.5rem;
}

.skills__name {
    width: 12rem;
    background-color: #232323;
    color: white;
    font-size: 1.5rem;
    text-align: center;

    display:flex;              /* RECAP centering */
    flex-direction:column;
    justify-content:space-around;
}

.skills__bar {
    width: 100%;
    background-color: #54be90;
    display: flex;
    justify-content: flex-end;
}

.skills__level {
    background-color:  #ebebeb;
    text-align: right;
    color: #444444;
    padding-right: 0.5rem;
    
    display:flex;              /* RECAP centering */
    flex-direction:column;
    justify-content:space-around;
}

.fb50 {flex-basis: 50%;}
.fb60 {flex-basis: 40%;}
.fb70 {flex-basis: 30%;}
.fb80 {flex-basis: 20%;}
.fb80 {flex-basis: 20%;}

/* portfolio */

.portfolio {
    background: #54be90;
    width: 100vw;
    color: black;
    padding: 0 0 4rem 0;
}

.projectsview {
    width: 80%;
    margin: auto;

    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
}

.project {
    background-color: white;
    display: flex;
    flex-flow: column nowrap;
    flex-basis: 31%;
    margin-bottom: 1.2rem;
    padding: 0.3rem;
}

.project__imgdiv {
    height: 8 rem;
}

.project__imgdiv > img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.project--text {
    color: black;
    text-align: center;
    padding: 0.5rem;
}


/* about me */

.about {
    background: white;
    width: 100vw;
    color: black;
    margin-bottom: 2rem;
    padding-bottom: 1rem;
}

.about__aneta {
    width: 70%;
    margin: auto;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.about__img {
    border-radius: 50%;
    width: 18rem;
    margin-right: 3rem;;
}

.about__imgdiv {
    flex: 0
}

.about__bio {
    flex: 1;
    text-align: left;

}

.about__ul {
    text-decoration: underline #54be90 0.3rem;
    text-underline-offset: 0.2rem;
}

/* BUTTON */

.button {
    color: #ffffff;
    background-color: #54be90;
    border-radius: 0.5rem;
    box-shadow: 2px 2px 8px 0px rgba(68, 68, 68, 0.5);
    font-size: 1.25rem;
    text-align: center;
    text-decoration: none;
    margin: 0 auto;
    padding: 0.5rem 3rem;
}

.button:hover {
	box-shadow: 2px 2px 8px 0px rgba(68, 68, 68, 0.8);
}

.social {
    width: 100vw;
    background-color: #232323;
    padding-bottom: 3rem;
}

.social__container {
    display: flex;
    width: 60%;
    margin: 0 auto;
    justify-content: space-between;
}


.social__link svg {
    width: 5em;
    height: 5em;

}

.social__link svg path {
    fill: white;
}

.social__link:hover svg path {
	opacity: 0.5;
}


/* Section Contact */
.contact {
	background-color: #54be90;
	//padding: 2rem 15% 6rem 15%;
}

.contact .heading {
	color: #ffffff;
}

.form {
	display: flex;
	flex-wrap: wrap;
    justify-content: space-between;
    width: 70%;
    margin: 0 auto;

}

.form__input {
	width: 48%;
	color: #ffffff;
	background: transparent;
	border: none;
	border-bottom: 1px solid #ebebeb;
	outline: none;
	font-size: 1.5rem;
	margin-bottom: 2rem;
	padding-top: 0.5rem;
	padding-bottom: 0.5rem;
}

.form__input::placeholder {
	color: #ebebeb;
}

.form__label {
	width: 100%;
	color: #ebebeb;
	font-size: 1.5rem;
	margin-bottom: 0.5rem;
}

.form__textarea {
	width: 100%;
	border: none;
	border-radius: 0.5rem;
	outline: none;
	font-family: 'Alegreya Sans', sans-serif;
	font-size: 1.5rem;
	margin-bottom: 2rem;
	padding: 0.5rem;
}

.form__button {
	width: 100%;
	color: #ffffff;
	background-color: #232323;
	border: none;
	border-radius: 0.5rem;
	font-size: 1.5rem;
	font-weight: bold;
	margin-bottom: 4rem;
	padding: 0.75rem;
	cursor: pointer;
}

.form__button:hover {
	box-shadow: 2px 2px 8px 0px rgba(68, 68, 68, 0.75);
}



//* Footer */
.footer {
	background-color: #ebebeb;
	padding: 4rem 15%;
}

.footer .heading {
  display: flex;
  align-items: center;
  justify-content: space-around;
	color: #232323;
	font-size: 3.5rem;
	font-weight: 800;
}

.footer__line {
	width: 25%;
	height: 0.5rem;
	background-color: #54be90;
}

.footer .social__link {
	margin: 0 2rem;
}

.footer .social__link svg {
	width: 4rem;
	height: 4rem;
}

.footer .social__link svg path {
	fill: #444444;
	opacity: 0.25;
}

.footer .social__link:hover svg path {
	opacity: 0.5;
}

.footer__copy {
	font-size: 1.5rem;
	text-align: center;
	margin-top: 4rem;
}